.btn {
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
  border-radius: 20px;
  transition: all 850ms;
  backface-visibility: hidden;
}
.btn.btn-color {
  background-color: #f6cb5e;
  text-transform: uppercase;
  padding: 5px 30px;
  font-size: 15px;
  font-weight: 600;
  letter-spacing: 1px;
  color: #fff;
  transition: background-color 350ms;
  border: 1px solid #fff;
}
.btn:hover.btn-color {
  background-color: #f2b416;
}
.btn:focus,
.btn:active {
  outline: none;
  box-shadow: none;
}
.nav-button-close {
  position: absolute;
  z-index: 11;
  width: 100%;
  top: 40px;
  left: 0;
}
.nav-button-close .container {
  display: flex;
  justify-content: flex-end;
}
.nav-button-close .container:before,
.nav-button-close .container:after {
  content: none;
}
.nav-button-close button {
  outline: none;
  border: none;
  background: none;
  padding: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nav-button-close button .button-bar {
  display: block;
  position: absolute;
  width: 20px;
  height: 1px;
  background: #222;
}
.nav-button-close button .button-bar:nth-child(1) {
  transform: rotate(45deg);
}
.nav-button-close button .button-bar:nth-child(2) {
  transform: rotate(-45deg);
}
#return-to-top {
  position: fixed;
  z-index: 333;
  bottom: 80px;
  right: 0;
  display: block;
  text-decoration: none;
  visibility: hidden;
  opacity: 0;
  transition: all 350ms ease;
  cursor: pointer;
  transform: translateY(100px) rotate(90deg);
}
#return-to-top.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0) rotate(90deg);
}
#return-to-top.white .line {
  background-color: #e7e6e4;
}
#return-to-top.white p {
  color: #fff;
}
#return-to-top.white:hover p {
  color: #fff;
}
#return-to-top .line {
  width: 120px;
  height: 0.5px;
  background-color: #f6cb5e;
}
#return-to-top p {
  font-size: 15px;
  color: #000;
  text-transform: uppercase;
  text-align: right;
  margin-right: 3px;
  transition: color 350ms;
  font-weight: 600;
}
#return-to-top:hover p {
  color: #f6cb5e;
}
.nav-button {
  padding: 10px 0;
  border: none;
  background: none;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  outline: none;
  display: flex;
}
.nav-button .button-bar {
  height: 1px;
  width: 24px;
  border-radius: 10px;
  transition: all 0.5s;
  background-color: #000;
}
.nav-button .button-bar + .button-bar {
  margin-top: 5px;
}
.nav-button:focus,
.nav-button:active {
  outline: none;
  box-shadow: none;
}
@font-face {
  font-family: 'Cormorant';
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/cormorant/v11/H4cgBXOCl9bbnla_nHIiRLmYgg.ttf) format('truetype');
}
@font-face {
  font-family: 'Cormorant';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/cormorant/v11/H4clBXOCl9bbnla_nHIa6A.ttf) format('truetype');
}
@font-face {
  font-family: 'Cormorant';
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/cormorant/v11/H4cgBXOCl9bbnla_nHIiML-Ygg.ttf) format('truetype');
}
@font-face {
  font-family: 'Cormorant';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/cormorant/v11/H4cgBXOCl9bbnla_nHIiVL6Ygg.ttf) format('truetype');
}
@font-face {
  font-family: 'Italianno';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/italianno/v11/dg4n_p3sv6gCJkwzT6Rnjw.ttf) format('truetype');
}
body {
  min-height: 100vh;
  height: 100%;
  font-family: "Cormorant", serif;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #f2f2f2;
  transition: background-color 350ms;
}
body.no-scroll {
  overflow: hidden;
  max-height: 100vh;
}
body ul,
body ol,
body p,
body h1,
body h2,
body h3,
body h4,
body h5,
body h6 {
  margin: 0;
  padding: 0;
}
body a {
  text-decoration: none !important;
}
.alert-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  background: rgba(0, 0, 0, 0.2);
  z-index: 555;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s;
}
.alert-wrapper .alert-service {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width: 100%;
  transform: translateX(8px);
  padding: 50px 0;
}
.alert-wrapper .alert-service .service-inner {
  padding: 50px;
  background: #fff;
  width: 100%;
  box-shadow: 0px 0px 32px 1px rgba(0, 0, 0, 0.15);
  text-align: center;
  border-top: 5px solid #fff;
  position: relative;
  transform: scale(0.8);
  transition: all 0.5s;
}
.alert-wrapper .alert-service .service-inner .inner-dismiss {
  top: 10px;
  right: 10px;
  position: absolute;
  border: 0;
  background: none;
  padding: 0;
  cursor: pointer;
  display: flex;
  padding: 5px;
  outline: none;
}
.alert-wrapper .alert-service .service-inner .inner-dismiss img {
  height: 15px;
}
.alert-wrapper .alert-service .service-inner.success {
  border-top: 5px solid #78b042;
}
.alert-wrapper .alert-service .service-inner.warning {
  border-top: 5px solid #d51a27;
}
.alert-wrapper .alert-service .service-inner.info {
  border-top: 5px solid #1a88d5;
}
.alert-wrapper.active {
  opacity: 1;
  visibility: visible;
}
.alert-wrapper.active .alert-service .service-inner {
  transform: none;
}
.select-wrapper {
  position: relative;
}
.select-wrapper label {
  color: #666;
}
.select-wrapper select {
  cursor: pointer;
  padding: 7px 100px 5px 10px;
  border: 1px solid #f6cb5e;
  border-radius: 4px;
  background: #fff;
  height: 31px;
  -moz-appearance: none;
  -webkit-appearance: none;
  font-size: 13px;
  appearance: none;
  outline: none;
  display: block;
  color: #666;
  width: 100%;
}
.select-wrapper:after {
  cursor: pointer;
  content: "";
  height: 10px;
  width: 10px;
  background: url("../img/pictures/down-chevron.svg") center;
  background-size: cover;
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}
.checkbox-wrapper {
  cursor: pointer;
  display: block;
  margin-bottom: 0;
}
.checkbox-wrapper .checkbox-box {
  position: relative;
  display: flex;
  align-items: center;
  font-size: 15px;
  text-transform: none;
  color: #777;
  text-align: left;
  transition: all 0.3s;
}
.checkbox-wrapper .checkbox-box:before {
  content: "";
  min-width: 16px;
  max-width: 16px;
  height: 16px;
  display: block;
  background: url("../img/pictures/checkbox.svg") no-repeat;
  margin-right: 10px;
  transition: all 0.3s;
}
.checkbox-wrapper input {
  position: absolute;
  opacity: 0;
  width: auto !important;
}
.checkbox-wrapper > input:checked + .checkbox-box {
  font-weight: 600;
  color: #e7e6e4;
}
.checkbox-wrapper > input:checked + .checkbox-box:before {
  background: url("../img/pictures/checkbox-checked.svg") no-repeat;
}
nav.section-header {
  transition: all 0.5s;
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  width: 100%;
}
nav.section-header .header-management {
  padding: 25px 0;
  max-height: 200px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all 0.5s;
}
nav.section-header .header-management .management-links {
  transition: all 0.5s;
}
nav.section-header .header-management .management-links ul {
  display: flex;
  align-items: center;
}
nav.section-header .header-management .management-links ul li a svg {
  width: 18px;
  height: 18px;
}
nav.section-header .header-management .management-links ul li + li {
  margin-left: 30px;
}
nav.section-header .header-management .management-logo {
  text-align: center;
  opacity: 0;
  transition: all 1s;
}
nav.section-header .header-management .management-logo a {
  display: block;
  position: relative;
  transition: all 0.4s;
  transform: translateY(10px) translateZ(0);
  color: #f6cb5e;
  letter-spacing: 1px;
  font-size: 90px;
  line-height: 45px;
  font-family: "Italianno", cursive;
}
nav.section-header .header-management .management-logo a span {
  display: block;
  font-size: 12px;
  line-height: 12px;
  font-weight: 700;
  letter-spacing: 1.5px;
  color: #000;
  font-family: "Cormorant", serif;
  text-transform: uppercase;
  transition: opacity 350ms;
}
nav.section-header .header-management .management-logo.opacity {
  opacity: 1;
}
nav.section-header .header-management .management-burger-menu {
  transition: all 0.5s;
  display: flex;
  justify-content: flex-end;
  position: relative;
}
nav.section-header .header-management .management-burger-menu .line {
  display: inline-block;
  height: 10px;
  width: 1px;
  color: #000;
  margin: 0 10px;
}
nav.section-header .header-management .management-burger-menu p {
  font-size: 20px;
}
nav.section-header.scrolled {
  background: rgba(231, 230, 228, 0.95);
}
nav.section-header.scrolled .header-management {
  padding: 5px 0 15px;
}
nav.section-header.scrolled .header-management .management-logo a {
  transform: translateY(10px) scale(0.7);
}
nav.section-header.scrolled .header-management .management-logo a span {
  opacity: 0;
}
nav.section-header .flexbox-nav-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  height: 100vh;
  width: 100%;
  background: rgba(0, 0, 0, 0.2);
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  overflow: hidden;
  transition: all 0.4s;
}
nav.section-header .flexbox-nav-wrapper.active {
  visibility: visible;
  opacity: 1;
  z-index: 111;
}
nav.section-header .flexbox-nav-wrapper.active .flexbox-nav-inner {
  transform: translateX(0);
}
nav.section-header .flexbox-nav-wrapper .flexbox-nav-inner {
  position: relative;
  left: 0;
  z-index: 55;
  height: 100%;
  width: 45%;
  transform: translateX(-100%);
  transition: all 0.4s;
  background-color: #fff;
}
nav.section-header .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
  padding-left: 30%;
  transition: all 0.4s;
  transform: translateX(-200px);
}
nav.section-header .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav.active {
  transform: translateX(0);
}
nav.section-header .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav .img-absolute {
  position: absolute;
  z-index: -1;
  top: 20%;
  left: -5%;
  width: 100%;
}
nav.section-header .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav li a {
  font-size: 20px;
  display: inline-block;
  padding: 5px 0;
  color: #000;
}
nav.section-header .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav li a.highlight {
  font-weight: 700;
  font-size: 28px;
}
nav.section-header .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav li.line {
  background-color: #000;
  width: 50%;
  height: 1px;
  margin: 20px 0 10px;
}
.section-wrapper {
  max-width: 1400px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}
section.section-welcome {
  position: relative;
  padding-top: 100px;
  background: url(../img/pictures/pattern.png) center no-repeat;
  background-size: cover;
}
section.section-welcome:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 800px;
  height: 105%;
  background-color: #e7e6e4;
}
section.section-welcome .wrapper-inner {
  position: relative;
  min-height: 75vh;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
section.section-welcome .wrapper-inner header {
  padding-right: 150px;
}
section.section-welcome .wrapper-inner h1 {
  font-size: 110px;
  font-weight: 600;
  line-height: 1.1;
  letter-spacing: 2px;
  margin-bottom: 20px;
}
section.section-welcome .wrapper-inner h1 p {
  font-weight: 500;
}
section.section-welcome .wrapper-inner h1 p:first-of-type {
  font-size: 56px;
}
section.section-welcome .wrapper-inner h1 p:nth-of-type(2) {
  font-size: 24px;
  padding-left: 150px;
  margin-top: 30px;
}
section.section-welcome .wrapper-inner h1 p:nth-of-type(3) {
  font-size: 48px;
  line-height: 50px;
  padding-left: 260px;
  color: #f6cb5e;
  font-family: "Italianno", cursive;
}
section.section-welcome .wrapper-icons {
  position: relative;
  background: linear-gradient(132deg, #ababab 0%, #e3e3e3 60%);
  height: 60px;
  padding: 0 20px;
  border-radius: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
  border: 1.5px solid #fff;
}
section.section-welcome .wrapper-icons .img-absolute {
  position: absolute;
  width: 45%;
  max-height: 700px;
  object-fit: contain;
  bottom: 56px;
  left: 0;
}
section.section-welcome .wrapper-icons .icon {
  font-size: 15px;
  letter-spacing: 1px;
  color: #000;
  display: flex;
  align-items: center;
}
section.section-welcome .wrapper-icons .icon .icon-image {
  width: 30px;
  height: 30px;
  border-radius: 100px;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
}
section.section-welcome .wrapper-icons .icon img {
  width: 20px;
}
section.section-about {
  padding-top: 150px;
}
section.section-about .about-inner {
  position: relative;
  background-color: #fff;
  box-shadow: 0px 20px 50px 1px rgba(155, 155, 155, 0.3);
  width: 100%;
  min-height: 300px;
  padding: 80px 50px;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
section.section-about .about-inner .about-image {
  object-fit: contain;
  margin-left: -300px;
}
section.section-about header {
  width: 30%;
}
section.section-about header h2 {
  font-size: 60px;
  line-height: 64px;
  font-weight: 600;
}
section.section-about .inner-description {
  width: 40%;
}
section.section-about p {
  font-size: 18px;
}
section.section-about p:not(:first-of-type) {
  margin-top: 15px;
}
section.section-about p strong {
  color: #f6cb5e;
}
@media (max-width: 767.98px) {
  nav.section-header {
    background-color: #f9f9f9;
  }
  nav.section-header .button-wrapper {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 15px;
    display: flex;
    justify-content: flex-end;
  }
  nav.section-header .button-wrapper .nav-button {
    display: flex;
  }
  nav.section-header .header-flexbox-wrapper {
    background-color: transparent;
  }
  nav.section-header .header-flexbox-wrapper .header-flexbox {
    justify-content: center;
    padding: 0;
  }
  nav.section-header .header-flexbox-wrapper .header-flexbox .flexbox-nav-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    overflow-y: scroll;
    background: #f6cb5e;
    z-index: 555;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s;
  }
  nav.section-header .header-flexbox-wrapper .header-flexbox .flexbox-nav-wrapper .nav-button-close {
    display: block;
  }
  nav.section-header .header-flexbox-wrapper .header-flexbox .flexbox-nav-wrapper .flexbox-nav-inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 100%;
    padding: 50px;
    margin-right: 0;
  }
  nav.section-header .header-flexbox-wrapper .header-flexbox .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav {
    flex-direction: column;
  }
  nav.section-header .header-flexbox-wrapper .header-flexbox .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav li {
    padding: 15px 0;
  }
  nav.section-header .header-flexbox-wrapper .header-flexbox .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav li.active a {
    background-color: transparent;
  }
  nav.section-header .header-flexbox-wrapper .header-flexbox .flexbox-nav-wrapper .flexbox-nav-inner .flexbox-nav li a {
    color: #000;
    padding: 0;
    font-size: 16px;
  }
  nav.section-header .header-flexbox-wrapper .header-flexbox .flexbox-nav-wrapper.active {
    opacity: 1;
    visibility: visible;
  }
}
